import LaunchApp from '../../components/LaunchApp.vue';
import FooterDownload from '../../components/FooterDownload.vue';

export default {
    name: 'Activity',
    components: {
        LaunchApp,
        FooterDownload
    },
    mounted() {
        this.getBanner();
        this.getActivityDetail();
        this.shareid = this.$util.getUrlParam('shareid');
    },
    filters: {
        formatTime(value) {
            return moment.utc(value).local().format('YYYY-MM-DD HH:mm');
        }
    },
    created() {

    },
    data() {
        return {
            activityData: '',
            participants: [],
            activityList: [],
            nowTime: moment(),
            shareid: '',
            banner: '',
            userCount: '',
            images: [],
            messages: {
                en: {
                    tweebaa: 'Tweebaa',
                    eventTime: 'Event',
                    signupTime: 'Signup',
                    participation: 'Participation',
                    address: 'Address',
                    registered: 'Registered',
                    unregistered: 'Unregistered',
                    details: 'Details',
                    moreInfo: 'More Events',
                    downloadApp: 'Download Tweebaa App Get T-beans',
                    platform: 'Tweebaa is the world\'s 1st multi-dimensional earning commerce and value-exchanging social networking platform allowing everyone to transform their knowledge, skills, interests, and experiences into creative products and discover their hidden values.',
                },
                zh: {
                    tweebaa: '推易吧',
                    eventTime: '活动时间',
                    signupTime: '报名时间',
                    participation: '活动人数',
                    address: '活动地址',
                    registered: '已报名',
                    unregistered: '暂无报名',
                    details: '活动详情',
                    moreInfo: '更多精彩内容',
                    downloadApp: '下载推易吧App 获取T豆奖励',
                    platform: '推易吧是全球首个支持创新变现的价值社交平台，您的知识、爱好、经验、影响力等个体价值可以在这里直接变现。',
                },
            }
        };
    },

    methods: {
        getBanner: function () {
            var i = Math.floor(Math.random() * 4) + 1;
            try {
                // this.banner = require('../../image/banner' + i + '-' + this.$util.language + '.jpg');
                this.banner = require('../../image/banner' + 1 + '-' + this.$util.language + '.jpg');
            } catch (e) {

            }
        },
        getActivityDetail: function () {
            var that = this;
            var activeId = this.$util.getUrlParam('id');
            var url = this.$util.getBaseUrl() + "api/charity/voics-activity/" + activeId;
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                // console.log(response);
                var loading = document.getElementById('loadingText');
                var parentObj = loading.parentNode;
                that.activityData = response.data.Data;
                that.participants = response.data.Users;
                that.userCount = response.data.UserCount;
                parentObj.removeChild(loading);
                setTimeout(function () {
                    initialize();
                });
            }).catch(function (error) {
                console.log(JSON.stringify(error) + '@@@@@@@@@@@@@@@@');
            });
        },
        getActivityList: function () {
            var url = this.$util.getBaseUrl() + "/api/voics/info/?currentpage=0&itemsperpage=5";
            var that = this;
            this.$util.getAxois(url, "GET", {}).then(function (response) {
                console.log(JSON.stringify(response));
                that.newsList = response.data.Data;
            }).catch(function (error) {
                console.log(JSON.stringify(error));
            });
        },
    }
}

//初始化图片显示
function initialize() {
    new Swiper('.swiper-container', {
        direction: 'horizontal',
        loop: false,
        initialSlide: 0,
        pagination: {
            el: '.swiper-pagination',
            type: 'bullets'
        }
    });
}
